* {
    box-sizing: border-box;
}

/* -------------------------------------------- */
/* global class and styles */
/* -------------------------------------------- */

body {
    font-family: 'Nunito Sans', sans-serif;
}

.container {
    max-width: 1140px;
    margin:0 auto;
    padding:0 15px;
}

.clearfix:after {
    content: "";
    display: table;
    clear: both;
}

.row {
    display: flex;
    margin-left:-15px;
    margin-right:-15px;
}
.row [class*="col-"] {
    padding: 0 15px;
}
.row .col-1-2 {
    width:50%;
}
.row .col-1-3 {
    width:33.3333%;
}
.row .col-1-4 {
    width:25%;
}

@media (max-width:760px) {
    .row {
        flex-direction: column;
    }
    .row [class*="col-"] {
        width:100%;
        margin-bottom:80px;
    }
}

.btn {
    font-size:16px;
    text-transform: uppercase;
    text-decoration: none;
    border-radius: 4px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0 10px;
    padding: 15px 35px;
    transition: 0.5s background;
}
.btn:hover {}

img {
    max-width: 100%;
    height: auto;
}

/*-----------------------------------------*/
/* main-hero */
/*-----------------------------------------*/

.main-hero {
    height: 100vh;
    min-height: 700px;
    background-color: #B60B4C;
    background-attachment: fixed;
    background-image: url(../images/main-header-bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    position: relative;
}

/*-------------------*/
/*----page-header----*/
/*-------------------*/

.page-header {
    background-color: #fff;
    min-height: 90px;
}

.page-header .container {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.page-logo {}

@media (max-width:760px) {
    .page-logo {
        margin:20px 0;
    }
    .page-header .container {
        flex-direction: column;
    }
}

.page-nav {
}

.page-nav-list {
    display: flex;
    width: 100%;
}

.page-nav-list li {
    margin-right: 15px;
}

.page-nav-list a {
    color: #849396;
    font-size: 1em;
    text-decoration: none;
    font-weight: bold;
    height: 88px;
    margin-right: 15px;
    transition: 0.4s color;
    text-align: center;
}

.page-nav-list li a:last-of-type {
    margin-right: 0;
}
.page-nav-list a:hover {
    color: #B60B4C;
}

/*------------------------------*/
/* main banner */
/*------------------------------*/

.main-banner {
    flex-grow:1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.main-banner-title {
    font-family: 'Satisfy', cursive;
    color: #fff;
    font-size: 4.5em;
    text-align: center;
}
    
.main-banner-text {
    color: #fff;
    max-width: 840px;
    font-size:17px;
    text-align: center;
    margin:30px auto;
}

.main-banner-buttons {}

.main-banner-buttons .btn {
    font-size: 16px;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
    color: #fff;
    border: 2px solid #fff;
    border-radius: 4px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0 10px;
    padding: 15px 35px;
    transition: 0.5s background; 
}

.main-banner-buttons .btn-primary:hover {
    background-image: linear-gradient( rgba(0,0,0,0.16), rgba(0,0,0,0.26)  );
}

/*-------------------------------*/
/*main-hero-arrow wave*/
/*-------------------------------*/

.main-hero-arrow {
    background: url("../images/header-addon.svg");
    width: 200px;
    height: 95px;
    transform: translate(-50%, 50%);
    background-repeat: no-repeat;
    position: absolute;
    left:50%;
    bottom:0;
}
.main-hero-arrow span {
    display: block;
    width:100%;
    height:100%;
    /* transform: rotate(-45deg); */
    position: relative;
}
.main-hero-arrow span i {
    display: block;
    width: 10%;
    height: 50%;
    background: url(../images/arrow.svg);
    background-repeat: no-repeat;
    background-position: center top;
    transform: translate(-50%, -50%);
    position:absolute;
    left:50%;
    top: 0 ;

    animation: arrowAnim 0.5s 0s infinite alternate;
}

@keyframes arrowAnim {
    0% {
        top:calc(50% - 2px);
    }
    100% {
        top:calc(50% + 2px);
    }
}
/*----------------------------*/
/*main-article*/
/*---------------------------*/

.main-articles-list {
    padding:100px 0;
}

.main-articles-list .container {}

.main-article-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.main-article-img-cnt {}

.main-article-img {
    border-radius: 50%;
}

.main-article-title {
    font-size:20px;
    font-weight: bold;
    color: #2f2f2f;
    margin:25px 0;
}

.main-article-text {
    color: #949494;
    margin-bottom:25px;
}

.main-service-btn {
    background-color: #EA4685;
    color: white;
    font-weight: bold;
}


/*----------------------------*/
/*main-introduction*/
/*---------------------------*/


.main-introduction {
    background-image: 
        linear-gradient(90deg, rgba(97, 92, 92, 0.5), transparent, rgba(219, 212, 212, 0.5)  ),
        linear-gradient( rgba(121, 114, 114, 0.36), rgba(0,0,0,0.36)  ),
        url("../images/paralax-bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    width: 100%;
    height: 300px;
    background-position: 100% 30%;

}

.main-introduction .container {
    padding: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main-introduction-title {
    font-family: 'Satisfy', cursive;
    color: #fff;
    font-size: 40px;
    text-align: center;
}
    
.main-introduction-text {
    color: #fff;
    max-width: 840px;
    font-size: 17px;
    text-align: center;
    margin:30px auto; 
    
}

/*----------------------------*/
/*main-goods-list*/
/*---------------------------*/

.main-goods-list {
    margin: 50px 0;
}

.main-goods-list .container {}

/*-------------------------------*/
/*main-form-list*/
/*------------------------------*/

.main-form-list {
    background-color: #EA4685;
    width: 100%;
    height: 300px;
    padding: 20px;
    display: flex;
}

.main-form-list .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main-form-title {
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    padding: 20px;
}
  
input[type="name"], input[type="email"] {
    border: 0.5px solid rgba(219, 212, 212, 0.5);
    border-radius: 8px;
    padding: 10px 20px 10px 20px;
    margin: 6px 0;
}
    
input[type="name"],input[type="email"]:focus {
    outline: none;
    border: 0.1em solid rgba(97, 92, 92, 0.5);
} 

  
input[type="submit"] {
    background: #EA4685;
    color: #fff;
    padding: 10px 30px;
    border: 1px solid #fff;
    border-radius: 5px;
    font-weight: bold;
}

input[type="submit"]:focus {
    outline: none;
    border: 1px solid rgba(219, 212, 212, 0.5);

}

@media (max-width:760px) {

    .form-list {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

}

/* -------------------------------------------- */
/* page map */
/* -------------------------------------------- */

#map {
    width: 100%;
    height: 1000px;
}

#map:before {
    content: "ljhfctygftgfg";
    display: block;
    position: absolute;
    width: 50px;
    height: 50px;
    left: 0;
    bottom: 0;
    background-image: url("../images/marker.png");
}



