/* -------------------------------------------- */
/* page footer */
/* -------------------------------------------- */

.page-footer {
    background: #191919;
    padding: 40px 0;
}

.page-footer-logo {
    margin-bottom:30px;
}

.page-footer-bar {
    display: flex;
    justify-content: space-between;
}

.page-footer-nav {
    display: flex;
}

.page-footer-nav-list {
    margin-right:40px;
}
.page-footer-nav-list li {
    padding-bottom: 5px;
}
.page-footer-nav-list a {
    color: rgba(247, 233, 233, 0.418);
    text-decoration: none;
    font-weight: 300;
    transition: 0.4s all;
}
.page-footer-nav-list a:hover {
    color: #EA4685;
}
.page-footer-copyright {
    margin-left: auto;
    color: rgba(247, 233, 233, 0.418);
}

@media (max-width:760px) {
    .page-footer {
        text-align: center;
    }
    .page-footer-bar {
        flex-direction: column;
    }
    .page-footer-nav {
        flex-direction: column;
    }
    .page-footer-nav-list {
        margin:0;
    }
    .page-footer-copyright {
        margin-left:0;
        display: block;
        margin-top:40px;
    }
}